// see https://ant.design/docs/spec/colors#Neutral-color-application
@border-color: @border-color-split;
@border-grids: sm @border-radius-sm, md @border-radius-md, lg @border-radius-lg;

/**
 * Set the four border to `1px`
 *
 *  `1px`
 */
.border {
  border: 1px solid @border-color !important;
}

@border-width-list: 0, 1;
.for(@border-width-list, {
  .border-css-mixin(@i) when(@i > 0) {
    @css-value-comment: ~'1px';
    @css-value: ~'@{i}px solid @{border-color}';
  }
  .border-css-mixin(@i) when(default()) {
    @css-value-comment: ~'0px';
    @css-value: 0;
  }
  .border-css-mixin(@adIndex);

  .border-@{adIndex} {
    e(~`genComment('@{css-value-comment}', 'Set the border size to {0}', ' {0}')`);
    border: @css-value !important;
  }
  .border-top-@{adIndex} {
    e(~`genComment('@{css-value-comment}', 'Set the top border size to {0}', ' {0}')`);
    border-top: @css-value !important;
  }
  .border-right-@{adIndex} {
    e(~`genComment('@{css-value-comment}', 'Set the right border size to {0}', ' {0}')`);
    border-right: @css-value !important;
  }
  .border-bottom-@{adIndex} {
    e(~`genComment('@{css-value-comment}', 'Set the bottom border size to {0}', ' {0}')`);
    border-bottom: @css-value !important;
  }
  .border-left-@{adIndex} {
    e(~`genComment('@{css-value-comment}', 'Set the left border size to {0}', ' {0}')`);
    border-left: @css-value !important;
  }
});

// color
.for-each(@colors, {
  .border-@{adKey} {
    @border-colors-color: extract(@adItem, @color-basic-position);
    e(~`genCommentColor('@{border-colors-color}', 'Set the border color to {0}', ' {0}')`);
    border-color: @border-colors-color !important;
  }
});

.for-each(@aliasColors, {
  .border-@{adKey} {
    e(~`genComment('@{adValue}', 'Set the border color to {0}', ' {0}')`);
    border-color: @adValue !important;
  }
});

// Border-radius
.for-each(@border-grids, {
  .rounded-@{adKey} {
    e(~`genComment('@{adValue}', 'Set the border radius to {0}', ' {0}')`);
    border-radius: @adValue;
  }
  .rounded-top-left-@{adKey} {
    e(~`genComment('@{adValue}', 'Set the border top-left corner to {0}', ' {0}')`);
    border-top-left-radius: @adValue;
  }
  .rounded-top-right-@{adKey} {
    e(~`genComment('@{adValue}', 'Set the border top-right corner to {0}', ' {0}')`);
    border-top-right-radius: @adValue;
  }
  .rounded-bottom-left-@{adKey} {
    e(~`genComment('@{adValue}', 'Set the border bottom-left corner to {0}', ' {0}')`);
    border-bottom-left-radius: @adValue;
  }
  .rounded-bottom-right-@{adKey} {
    e(~`genComment('@{adValue}', 'Set the border bottom-right corner to {0}', ' {0}')`);
    border-bottom-right-radius: @adValue;
  }
});

/**
 * Set circular border
 *
 * 
 */
.rounded-circle {
  border-radius: 50%;
}

/**
 * Set frameless
 *
 * 
 */
.rounded-0 {
  border-radius: 0;
}

/**
 * Set the border color to white
 *
 * 
 */
.border-white {
  border-color: #fff !important;
}

/**
 * TODO:  stylelint 
 */
.rotate-loop(@i) when (@i > 0) {
  @num: @i * 15;
  .rotate-@{num} {
    e(~`genComment('@{num}', 'Element transform to {0} degree', ' {0} ')`);
    transform: rotate(~'@{num}deg');
  }
  .rotate-loop(@i - 1);
}

.rotate-loop(@rotate-count);
